import { Card, Select, TextInput, Button, Table, Group, Text, Modal } from '@mantine/core';
import { useForm } from '@mantine/form';
import React, { useEffect, useState } from "react";
import { createSearchParams, useNavigate } from "react-router-dom";
import { HeaderResponsive } from "./header";
import request from 'sync-request';
import { modals } from '@mantine/modals';
import { useDisclosure } from '@mantine/hooks';




const Invoice = (props) => (
    <tr>
        <td>{props.invoice.IssueDate}</td>
        <td>{props.invoice.DueDate}</td>
        <td>{props.invoice.PayableAmount}</td>
        <td>{props.invoice.BuyerRef}</td>
        <td>{props.invoice.SalesOrderId}</td>
        <td> <Button onClick={() => { props.edit(props.invoice._id)}}> Edit </Button></td>
        <td> <Button onClick={() => { props.view(props.invoice._id)}}> View </Button></td>
        <td> <Button className="btn btn-link"
            onClick={() => {
                props.deleteInvoice(props.invoice._id);
            }}
        > Delete </Button>
        </td>
    </tr>
);





export default function InvoicesList({ access_token }) {
    const navigate = useNavigate();
    const [invoices, setInvoices] = useState([]);
    const [sortby, setSortby] = useState(null);
    const [sortfor, setSortfor] = useState(null);
    const [userDetails, setUserDetails] = useState(null);
    const [opened, { open, close }] = useDisclosure(false); // for modals

    const create = () =>{
        console.log(JSON.parse(userDetails).sub)
        navigate({
            pathname:'/create',
            search: createSearchParams({
                id: JSON.parse(userDetails).sub
            }).toString()
        })
    }

    const edit = (Invoice_id) =>{
        console.log(JSON.parse(userDetails).sub)
        navigate({
            pathname:'/edit',
            search: createSearchParams({
                invoiceId: Invoice_id
            }).toString()
        })
    }

    const view = (Invoice_id) =>{
        console.log(JSON.parse(userDetails).sub)
        navigate({
            pathname:'/view',
            search: createSearchParams({
                invoiceId: Invoice_id
            }).toString()
        })
    }



    //set the userdetails using the access token
    useEffect(() => {
        console.log(access_token);
       
        // fetch(`http://localhost:8080/invoices/userinfo/${access_token}`, {
        //     method: 'GET',})
        // .then(response => response.text())
        // .then(result => console.log(result))
        // .catch(error => console.log('error', error));

        const res = new request(
            'GET',
            `http://localhost:8080/invoices/userinfo/${access_token}`,
        );
        const user = res.body;
        console.log(user);
        setUserDetails(user);
        // getAll();
        return;
    }, [access_token]);



    const confirmDelete = (id) => modals.openConfirmModal({
        title: 'Are you sure you want to delete this Invoice?',
        children: (
            <Text size="sm">
                Once you click delete it will be gone forever and you will not be able to retrieve it.
            </Text>
        ),
        labels: { confirm: 'Confirm', cancel: 'Cancel' },
        onCancel: () => {
            console.log('Cancel');
            return;
        },
        onConfirm: () => {
            console.log('Confirmed');
            deleteInvoice(id);
        }
    });


   

    const form = useForm({
        initialValues: {
            sortby: "IssueDate",
            sortfor: ""
        }
    });



    async function queryDatabase(formvalues) {

        console.log(formvalues);
        const res = new request(
            'GET',
            `http://localhost:8080/invoices/query?sortby=${formvalues.sortby}&sortfor=${formvalues.sortfor}&username=${userDetails.sub}`,
        );
        const invoices = JSON.parse(res.body);
        if (invoices.length == 0) {
            window.alert("No results for this query");
        } else {
            console.log(invoices);
        }
        setInvoices(invoices);

    }

    // This method fetches the invoices from the database.

    async function getAll() {
        const username = JSON.parse(userDetails).sub;
        console.log(username);
        const res = new request(
            'GET',
            `http://localhost:8080/invoices?Username=${username}`,
        );
        const invoices = JSON.parse(res.body);
        setInvoices(invoices);
    }




    // This method will delete a invoice
    async function deleteInvoice(id) {
        console.log(id)
        await fetch(`http://localhost:8080/invoices/${id}`, {
            method: "DELETE"
        });

        const newRecords = invoices.filter((el) => el._id !== id);
        setInvoices(newRecords);
    }

    // This method will map out the invoices on the table
    function invoiceList() {
        return invoices.map((invoice) => {
            return (
                <Invoice
                    username={invoice.Username}
                    invoice={invoice}
                    deleteInvoice={() => confirmDelete(invoice._id)}
                    edit={() => edit(invoice._id)}
                    key={invoice._id}
                    view={() => view(invoice._id)}
                />
            );
        });
    }

    // Everything returned
    return (
        <div>

            <HeaderResponsive />

            <Modal opened={opened} onClose={close}>
                <form onSubmit={form.onSubmit((values) => { queryDatabase(values) })}>
                    <Select
                        placeholder="Pick one"
                        data={['IssueDate', 'DueDate', 'PayableAmount', 'BuyerRef', 'SalesOrderId']}
                        label="Search by:"
                        value={sortby}
                        onChange={setSortby}
                        {...form.getInputProps('sortby', { type: 'Select' })}
                    />
                    <TextInput
                        withAsterisk
                        label="Search for:"
                        placeholder="09/10/2003"
                        value={sortfor}
                        onChange={setSortfor}
                        {...form.getInputProps('sortfor', { type: 'TextInput' })}
                    />


                    <Group position="right" mt="md">
                        <Button type="submit" fullWidth onClick={close} mt="md">
                            Apply
                        </Button>
                    </Group>
                </form>
            </Modal>

            <div style={{ margin: "100px", alignItems: "center", color: "white", width:"1000px" }}>

                <Group mt="md" mb="xs">
                    <Button onClick={open}>Filter</Button>
                    <Button onClick={() => getAll()}>All</Button>
                    <Button onClick={create}>+ Create new Invoice</Button>
                </Group>

                <div style={{ margin: "50px"}}>


                    <Card shadow="sm" padding="lg" radius="md" withBorder>

                        <Table >
                            <thead>
                                <tr>
                                    <th>IssueDate</th>
                                    <th>DueDate</th>
                                    <th>PayableAmount</th>
                                    <th>BuyerRef</th>
                                    <th>SalesOrderId</th>
                                    <th>Edit</th>
                                    <th>View</th>
                                    <th>Delete</th>
                                </tr>
                            </thead>
                            <tbody>{invoiceList()}</tbody>
                        </Table>
                    </Card>
                </div>

            </div>
        </div >
    );
}
