
import { Card, Button, LoadingOverlay, Box, Select, Group, TextInput } from "@mantine/core";
import { HeaderResponsive } from "./header";
import React, { useState } from "react";
import { useSearchParams } from 'react-router-dom';
import { useForm } from '@mantine/form';




export default function View() {
    const [visible, setVisible] = useState(false);
    const [accessKey, setkey] = useState("");
    const [searchparams] = useSearchParams();
    const [content, setContent] = useState(null);
    const url = "https://macroservices.masterofcubesau.com/api/v2";
    console.log(searchparams.get("invoiceId"))
    const [chosenLanguage, setLanguage] = useState("");
    const [displaylanguage, setdisplay] = useState("");
    const [email, setemail] = useState("");

    const form = useForm({
        initialValues: {
            language: "en",
            email: "",
        },
        validate: {
            email: (value) => (/^\S+@\S+$/.test(value) ? null : 'Invalid email'),
        }
    });

    const submitForm = (values) => {
        setdisplay(values.language);
        console.log(values);
        if (values.language == "english") {
            setLanguage("en");
        } else if (values.language == "korean") {
            setLanguage("ko");
        } else if (values.language == "spanish") {
            setLanguage("es");
        } else if (values.language == "mandarin") {
            setLanguage("zh");
        } else if (values.language == "japanese") {
            setLanguage("ja");
        }
        setemail(values.email);
        getxmlfile();
    }


    // This method will delete a invoice
    async function getxmlfile() {
        var requestOptions = {
            method: 'GET',
            redirect: 'follow'
        };

        console.log("getting");
        fetch(`http://localhost:8080/get?id=${searchparams.get("invoiceId")}`, requestOptions)
            .then(response => response.text())
            .then(result => {
                console.log(result);
                setContent(result);
                fetch("https://macroservices.masterofcubesau.com/api/v2/generatekey", requestOptions)
                    .then(response => response.text())
                    .then(result => {
                        console.log(result);
                        setkey(JSON.parse(result).key);
                    })
                    .catch(error => console.log('error', error));
            })
            .catch(error => console.log('error', error));

    }

    async function render() {
        setVisible(true);
        const style = 0;
        const language = chosenLanguage;
        const invoicename = "fileone"
        const filename = "AU_" + invoicename + "_style" + style + "_" + language + ".pdf";
        var myHeaders = new Headers();
        myHeaders.append("api-key", accessKey);
        myHeaders.append("Content-Type", "application/json");

        var raw = JSON.stringify({
            "ubl": content,
            "style": style,
            "language": chosenLanguage,
            "optional": {
                "icon": "data:image/png;base64,<base64string>"
            }
        });

        var requestOptions = {
            method: 'POST',
            headers: myHeaders,
            body: raw,
            redirect: 'follow',
            responseType: 'stream',
        };



        fetch("https://macroservices.masterofcubesau.com/api/v2/invoice/render/pdf", requestOptions)
            .then(response => response.blob())
            .then(blob => URL.createObjectURL(blob))
            .then(uril => {
                setVisible(false);
                var link = document.createElement("a");
                link.href = uril;
                link.download = filename;
                document.body.appendChild(link);
                link.click();
                document.body.removeChild(link);
            });
    }

    async function sendFile() {
        setVisible(true);
        const style = 0;
        const language = chosenLanguage;
        const invoicename = "fileone"
        const filename = "AU_" + invoicename + "_style" + style + "_" + language + ".pdf";
        var myHeaders = new Headers();
        myHeaders.append("api-key", accessKey);
        myHeaders.append("Content-Type", "application/json");

        var raw = JSON.stringify({
            "ubl": content,
            "style": style,
            "language": chosenLanguage,
            "optional": {
                "icon": "data:image/png;base64,<base64string>"
            }
        });

        var requestOptions = {
            method: 'POST',
            headers: myHeaders,
            body: raw,
            redirect: 'follow',
            responseType: 'stream',
        };

        fetch("https://macroservices.masterofcubesau.com/api/v2/invoice/render/pdf", requestOptions)
            .then(response => response.blob())
            .then(blob => blobToBase64(blob))
            .then(base64 => {
                const data = { pdfFileData: base64 };
                return fetch(`http://localhost:8080/send-email?sendto=${email}`, {
                    method: "POST",
                    headers: { "Content-Type": "application/json" },
                    body: JSON.stringify(data),
                });
            })
            .then(response => response.text())
            .then(result => {
                console.log(result);
                window.alert(result);
                setVisible(false);
            })
            .catch(error => console.error(error));
        function blobToBase64(blob) {
            return new Promise((resolve, reject) => {
                const reader = new FileReader();
                reader.onload = () => resolve(reader.result.split(',')[1]);
                reader.onerror = error => reject(error);
                reader.readAsDataURL(blob);
            });
        }
    }


    return (
        <div>

            <HeaderResponsive />
            <Box pos="relative">
                <LoadingOverlay transitionDuration={500} visible={visible} overlayBlur={2} />
                <div style={{ margin: "100px", alignItems: "center", color: "white", width: "800px", heigth: "100vh" }}>
                    <Card shadow="sm" padding="lg" radius="md" withBorder>

                        <form onSubmit={form.onSubmit((values) => { submitForm(values) })}>
                            <Select
                                placeholder="Pick one"

                                data={['english', 'mandarin', 'korean', 'spanish', 'japanese']}
                                label="Language:"
                                value={chosenLanguage}
                                {...form.getInputProps('language', { type: 'Select' })}
                            /><br></br>
                            <TextInput
                                withAsterisk
                                label="Email to..."
                                placeholder="email1@gmail.com"
                                value={email}
                                {...form.getInputProps('email', { type: 'TextInput' })}
                            />

                            <Group position="right" mt="md" >
                                <Button type="submit" mt="md">
                                    Apply
                                </Button>
                            </Group>

                        </form>
                        <br></br>
                        <div>You have chosed to email <b>{email} </b>in <b> {displaylanguage}</b></div>
                        <br></br>
                        <br></br>

                        <Group spacing="lg" position="apart">

                            <Button onClick={() => render()}>Download</Button>
                            <Button onClick={() => sendFile()}>Email</Button>
                        </Group>


                    </Card>
                </div>
            </Box>
        </div>

    );
}



