import { LoadingOverlay, Button, Group, Box, Card } from '@mantine/core';
import React, { useState } from "react";
import { HeaderResponsive } from "./header";
import { useForm } from "react-hook-form";
import { useSearchParams } from 'react-router-dom';


export default function Create() {
  const [visible, setVisible] = useState(false);
  const [disabled, setDisabled] = useState(true);
  const [searchparams] = useSearchParams();
  const { register, handleSubmit } = useForm();
  const [obj, setobj] = useState({});
  const testUser = "testig123";

  const onSubmit = (data) => {
    const formData = new FormData();
    formData.append("myfile", data.file[0]);
    fetch(`http://localhost:8080/upload`, {
      method: "POST",
      body: formData,
    })
      .then(res => res.json())
      .then(data => {
        let obj = JSON.parse(data.xml);
        obj.Username = searchparams.get("id");
        console.log(obj);
        setobj(obj);
        setDisabled(false);
      })
      .catch((err) => window.alert(err));

  };

  async function createInvoice() {
    setVisible(true);
    var myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");
    var raw = JSON.stringify(obj);
    var requestOptions = {
      method: 'POST',
      headers: myHeaders,
      body: raw,
      redirect: 'follow'
    };
    
    fetch("http://localhost:8080/invoices/create", requestOptions)
      .then(response => response.text())
      .then(result => {
        console.log(result)
        setVisible(false);
        window.alert(result);
      }
      )
      .catch(error => console.log('error', error));
  }


  return (
    <div>
      <HeaderResponsive />
      <Box pos="relative">
        <LoadingOverlay transitionDuration={500} visible={visible} overlayBlur={2} />
        <div style={{ margin: "100px", alignItems: "center", color: "white" }}>
          <Card shadow="sm" padding="lg" radius="md" withBorder>

            <form onSubmit={handleSubmit(onSubmit)}>
              <input {...register("file")} type="file" />
              <Button type="submit">upload</Button><br></br>
   
              <Group position="right" mt="md"><Button disabled={disabled} onClick={() => createInvoice()}>create new invoice</Button></Group>

            </form>
          </Card>
        </div>
      </Box>


    </div>
  );
}